import { printLine } from './modules/print';

var duration;
var pageHasVideo;

// chrome.tabs.onUpdated.addListener(function(tabId, changeInfo, tab) {
//   chrome.tabs.sendMessage(tabs[0].id, {type: "urlUpdated"}); // sends msg to popup when url changes
// });

window.onload = function () {
  if (document.getElementsByTagName("video")[0]) {
    // video exists on the page
    duration = document.getElementsByTagName("video")[0].duration;
    pageHasVideo = true;
  } else {
    pageHasVideo = false;
    //TODO: if page doesn't have video, show dialogue
  }

  chrome.runtime.onMessage.addListener(
      function(message, sender, sendResponse) {
          switch(message.type) {
              case "pageHasVideo":
                  sendResponse(pageHasVideo);
                  break;
              case "getDuration":
                  sendResponse(duration);
                  break;
              default:
                  console.error("Unrecognised message: ", message);
          }
      }
  );
}.bind(this);
